	module.exports = {"pathContext":{"docHtml":"<h1 id=\"release-instructions\"><a href=\"#release-instructions\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Release Instructions</h1>\n<ol>\n<li>\n<p>Update CHANGELOG.md with changes in the release</p>\n</li>\n<li>\n<p>Update VERSION with new tag</p>\n</li>\n<li>\n<p>Update codegen, manifests with new tag</p>\n</li>\n</ol>\n<pre><code>make codegen manifests IMAGE_NAMESPACE=argoproj IMAGE_TAG=vX.Y.Z\n</code></pre>\n<ol start=\"4\">\n<li>\n<p>Commit VERSION and manifest changes</p>\n</li>\n<li>\n<p>git tag the release</p>\n</li>\n</ol>\n<pre><code>git tag vX.Y.Z\n</code></pre>\n<ol start=\"6\">\n<li>Build the release</li>\n</ol>\n<pre><code>make release IMAGE_NAMESPACE=argoproj IMAGE_TAG=vX.Y.Z\n</code></pre>\n<ol start=\"7\">\n<li>\n<p>If successful, publish the release:</p>\n<pre><code>export ARGO_RELEASE=vX.Y.Z\ndocker push argoproj/workflow-controller:${ARGO_RELEASE}\ndocker push argoproj/argoexec:${ARGO_RELEASE}\ndocker push argoproj/argocli:${ARGO_RELEASE}\ngit push upstream ${ARGO_RELEASE}\n</code></pre>\n</li>\n<li>\n<p>Draft GitHub release with the content from CHANGELOG.md, and CLI binaries produced in the <code>dist</code> directory</p>\n</li>\n<li>\n<p><a href=\"https://github.com/argoproj/argo/releases/new\">https://github.com/argoproj/argo/releases/new</a></p>\n</li>\n</ol>","docPath":"argo/docs/releasing","proj":"argo"}}

